}
.product__text {
  font-family: "Lato", "Times New Roman", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  color: #444444;
  text-align: center;
  margin: 0;
  padding: 10px 0 10px 0;
}
.product__redirect {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  box-sizing: border-box;
  min-height: 40px;
  padding: 10px;
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #444444;
  background-color: #f2f2f2;
  overflow: hidden;
  text-overflow: ellipsis;
  border: none;
  outline: none;
  cursor: pointer;
}
.product__redirect:hover {
  color: rgba(68, 68, 68, 0.6);
  background-color: rgba(235, 235, 235, 0.6);
}
.product__redirect:focus {
  color: rgba(68, 68, 68, 0.6);
  background-color: rgba(235, 235, 235, 0.6);
  outline: none;
}
.product__redirect:active {
  color: rgba(68, 68, 68, 0.3);
  background-color: rgba(235, 235, 235, 0.3);
}
@media (min-width: 768px) {
