.subproduct-item {
  align-items: center;
  align-self: center;
  justify-content: center;
  border: 1px solid #e6e6e6;
  border-left: none;
  border-right: none;
  margin: 0;
  padding-top: 20px;
  padding-bottom: 20px;
  box-sizing: border-box;
}
.subproduct-item:not(:last-child) {
  border-bottom: none;
}
.subproduct-item__title {
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  color: #222222;
  text-align: left;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
}
.subproduct-item__annotation {
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
}
.subproduct-item__label {
  font-family: "Lato", "Times New Roman", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  color: #444444;
  text-align: center;
  margin: 0;
}
.subproduct-item__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 40px;
  box-sizing: border-box;
  padding: 5px 10px;
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  overflow: hidden;
  text-overflow: ellipsis;
  border: none;
  outline: none;
  cursor: pointer;
  width: 100%;
}
.subproduct-item__btn:hover {
  color: rgba(255, 255, 255, 0.6);
  background-color: rgba(94, 170, 47, 0.6);
}
.subproduct-item__btn:focus {
  color: rgba(255, 255, 255, 0.6);
  background-color: rgba(94, 170, 47, 0.6);
  outline: none;
}
.subproduct-item__btn:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: rgba(94, 170, 47, 0.3);
}
@media (min-width: 768px) {
  .subproduct-item {
    display: grid;
    grid-template-columns: 187px 400px;
    grid-template-rows: repeat(2, auto);
    row-gap: 20px;
    padding: 20px 40px;
    border: 1px solid #e6e6e6;
  }
  .subproduct-item__title {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    font-size: 20px;
    line-height: 20px;
  }
  .subproduct-item__annotation {
    grid-column: 2 / 3;
    grid-row: 1 / 2;
    column-gap: 10px;
    justify-content: flex-end;
    align-items: center;
    margin: 0;
  }
  .subproduct-item__label {
    font-size: 16px;
    line-height: 20px;
    text-align: right;
    min-width: 135px;
  }
  .subproduct-item__btn {
    grid-row: 2 / 3;
    grid-column: 1 / -1;
  }
}
@media (min-width: 1440px) {
  .subproduct-item {
    grid-template-columns: 250px 322px 244px;
    grid-template-rows: auto;
    column-gap: 40px;
    border-left: none;
    border-right: none;
    margin: 0;
    padding: 15px 0;
  }
  .subproduct-item__title {
    grid-column: 1 / 2;
    font-size: 20px;
    line-height: 20px;
    margin: 0;
  }
  .subproduct-item__annotation {
    grid-column: 2 / 3;
    justify-content: space-between;
  }
  .subproduct-item__label--start {
    text-align: left;
  }
  .subproduct-item__btn {
    grid-column: 3 / 4;
    grid-row: 1 / 2;
  }
}
.present {
  position: relative;
  display: block;
  min-height: 290px;
  background-image: url("../img/present/mobile-cat-inthebag@1x.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .present {
    background-image: url("../img/present/mobile-cat-inthebag@2x.png");
  }
}
.present::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 290px;
  background-color: rgba(104, 183, 56, 0.85);
}
.present__text {
  font-family: "Lato", "Times New Roman", sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
  padding: 160px 40px 5px 40px;
  margin: 0;
  box-sizing: border-box;
  position: relative;
  z-index: 2;
}
.present__text::after {
  content: "";
  position: absolute;
  top: 25px;
  left: 100px;
  width: 78px;
  height: 80px;
  mask: url("../img/steck.svg#icon_present");
  mask-size: cover;
  background-color: #ffffff;
  background-repeat: no-repeat;
  z-index: 2;
}
@media (min-width: 768px) {
  .present {
    background-image: url("../img/present/tablet-cat-inthebag@1x.webp");
  }
  .present::after {
    width: 100%;
    height: 100%;
  }
  .present__text {
    padding: 115px 70px 50px 400px;
    text-align: left;
  }
  .present__text::after {
    top: 90px;
    left: 50px;
    width: 80px;
    height: 83px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .present {
    background-image: url("../img/present/tablet-cat-inthebag@2x.png");
  }
}
@media (min-width: 1440px) {
  .present {
    position: relative;
    background-image: url("../img/present/mobile-cat-inthebag@1x.webp");
  }
  .present__text {
    padding: 160px 45px 5px 35px;
    text-align: center;
  }
  .present__text::after {
    top: 25px;
    left: 76px;
    width: 78px;
    height: 80px;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .present {
    background-image: url("../img/present/mobile-cat-inthebag@2x.png");
  }
}
.connection {
  position: relative;
  margin: 0;
  padding: 0;
}
.connection__contact {
  position: relative;
}
.contact {
  padding: 30px 0 0 0;
  position: relative;
}
.contact__container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: repeat(2, auto);
  column-gap: 18px;
  min-height: 60px;
}
@media (min-width: 768px) {
  .contact__container {
    max-width: 708px;
  }
}
@media (min-width: 1440px) {
  .contact__container {
    max-width: 1220px;
  }
}
.contact__title {
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: #111111;
  text-transform: uppercase;
}
.contact__text {
  font-family: "Lato", "Times New Roman", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #444444;
}
.contact__text > span {
  display: block;
}
.contact--catalog {
  background-color: #f2f2f2;
}
.contact--form {
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .contact__container > p {
    padding: 60px 40px 40px 40px;
    margin: 0;
  }
  .contact__title {
    font-size: 20px;
    line-height: 26px;
  }
  .contact__text {
    font-size: 16px;
    line-height: 20px;
    text-align: right;
  }
  .contact__text > span {
    margin-top: 20px;
  }
}
@media (min-width: 1440px) {
  .contact {
    width: 1220px;
    margin: 0 auto;
    padding: 0;
    z-index: 2;
  }
  .contact__container {
    content: "";
    position: absolute;
    top: 95px;
    left: 0;
    width: 565px;
    min-height: 199px;
    padding: 20px 30px 20px 30px;
    background-color: #ffffff;
    z-index: 2;
  }
  .contact__container > p {
    padding: 60px 50px 30px 30px;
    text-align: left;
  }
  .contact--catalog {
    background-color: transparent;
  }
  .contact--form {
    background-color: transparent;
  }
}
.map {
  position: relative;
}
.map__spb {
  display: block;
  width: 100%;
  min-height: 362px;
  border: none;
  position: relative;
  z-index: 1;
}
.map__spb::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("../img/pictures/logo_pin.png");
  width: 57px;
  height: 53px;
  z-index: 1;
}
@media (min-width: 768px) {
  .map__spb::after {
    width: 113px;
    height: 106px;
  }
}
.footer {
  position: relative;
  background-color: #f2f2f2;
}
.footer__container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
  display: grid;
  place-items: center;
  row-gap: 18px;
  min-height: auto;
  padding: 50px 20px;
}
@media (min-width: 768px) {
  .footer__container {
    max-width: 708px;
  }
}
@media (min-width: 1440px) {
  .footer__container {
    max-width: 1220px;
  }
}
.footer__logo {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.footer__social {
  display: flex;
  min-width: 132px;
  min-height: 22px;
}
.footer__partner {
  display: flex;
  min-height: 34px;
}
.footer__line {
  width: 100%;
  border: none;
  outline: none;
  height: 1px;
  background-color: #d9d9d9;
  margin: 0;
}
@media (min-width: 768px) {
  .footer__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    min-height: 147px;
    padding: 0 20px;
    box-sizing: border-box;
  }
  .footer__partner {
    width: 168px;
  }
  .footer__line {
    display: none;
  }
}
@media (min-width: 1440px) {
  .footer__container {
    min-height: 169px;
  }
  .footer__line {
    display: none;
  }
}
.social__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  min-width: 132px;
  column-gap: 10px;
  margin: 0;
}
.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px;
  box-sizing: border-box;
  background-position: center;
  fill: #666666;
  border: none;
  outline: none;
  cursor: pointer;
}
.social__icon {
  display: block;
  width: 28px;
  height: 22px;
}
.social__link:hover {
  fill: #68b738;
}
.social__link:focus {
  fill: #68b738;
}
.social__link:active {
  fill: rgba(104, 183, 56, 0.3);
}
.partner__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  min-width: 280px;
  min-height: 34px;
  margin: 0;
  padding: 0;
}
.partner__link {
  background-position: center;
  text-decoration: none;
  border: none;
  outline: none;
  cursor: pointer;
}
.partner__text {
  font-family: "Lato", "Times New Roman", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #444444;
}
.partner__text:hover {
  color: rgba(68, 68, 68, 0.6);
}
.partner__text:focus {
  color: rgba(68, 68, 68, 0.6);
}
.partner__text:active {
  color: rgba(68, 68, 68, 0.3);
}
.partner__icon {
  display: block;
  width: 27px;
  height: 34px;
  border: none;
  fill: #666666;
}
.partner__icon:hover {
  fill: #68b738;
}
.partner__icon:focus {
  fill: #68b738;
}
.partner__icon:active {
  fill: rgba(104, 183, 56, 0.3);
}
@media (min-width: 768px) {
  .partner__list {
    min-width: 168px;
  }
}
